  name: 'DepositPageInfo',
  components: {
    QrCode,
    Icon,
    Input,
    OrDivider
  },
  setup (_, { emit }) {
    const { t, locale } = useI18n()
    const { isMobile } = useBreakpointsMedia()
    const coinAmount = ref(20.62151445)
